<template>
  <v-container>
    <v-layout row>
      <v-flex xs12>
        <h1 class="text--secondary" mb-3>My Books</h1>

        <v-layout row justify-center>
          <v-btn color="primary" dark @click.native.stop="dialog = true">Add</v-btn>
          <v-dialog v-model="dialog" max-width="1000">
            <v-card>
              <v-card-text>
                <v-text-field name="name" label="Add name" type="text"
                              v-model="name"

                ></v-text-field>
                <v-text-field name="author" label="Add author" type="text"

                              v-model="author"

                ></v-text-field>
                <v-text-field name="year" label="Add year" type="text"

                              v-model="year"
                              :rules="[v => !!v || 'Year is required']"
                ></v-text-field>
                <v-text-field name="image" label="Add image src" type="text"

                              v-model="imgSrc"

                ></v-text-field>
                <v-btn @click="sendForm()" class="success">Add book</v-btn>
              </v-card-text>
            </v-card>
          </v-dialog>
        </v-layout>

        <v-layout row justify-center>
          <v-dialog v-model="dialog" max-width="1000">
            <v-card>
              <v-card-text>
                <v-text-field name="name" label="Add name" type="text"
                              v-model="books.name"

                >{{}}</v-text-field>
                <v-text-field name="author" label="Add author" type="text"

                              v-model="books.author"

                >{{books.author}}</v-text-field>
                <v-text-field name="year" label="Add year" type="text"

                              v-model="books.year"
                              :rules="[v => !!v || 'Year is required']"
                >{{books.year}}</v-text-field>
                <v-text-field name="image" label="Add image src" type="text"

                              v-model="books.imgSrc"

                >{{books.year}}</v-text-field>
                <v-btn class="warning" flat @click="completeBook(book.id)" dark @click.native.stop="dialog = false">Edit</v-btn>
              </v-card-text>
            </v-card>
          </v-dialog>
        </v-layout>


        <v-card v-for="book of books" class="mb-4">
          <v-card-media
            :src="book.imgSrc"
            :key="book.id"
            height="1000px"
          ></v-card-media>
          <v-card-text>
            <h1 class="text--primary">{{book.author}}</h1>
            <h3 class="text--primary">{{book.name}}</h3>
            <p>{{book.year}}</p>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn class="warning" flat @click="completeBook()" dark @click.native.stop="dialog = true">Edit</v-btn>

          </v-card-actions>

        </v-card>


      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    data() {
      return {
        id: '',
        name: '',
        author: '',
        year: '',
        imgSrc: '',
        dialog: false,
        books: [{
          id: 1,
          name: 'Незнайка на луне',
          author: 'Носов Н.Н.',
          year: '2013',
          isRead: false,
          page: '',
          imgSrc: 'data:image/jpeg;base64,'
        },
          {
            id: 2,
            name: 'Дневник Коли Синицына',
            author: 'Носов Н.Н.',
            year: '2009',
            isRead: false,
            page: '',
            imgSrc: 'http://www.planetaskazok.ru/images/stories/nosov/fantazery/img_2.jpg'
          },
          {
            id: 3,
            name: 'Фантазёры',
            author: 'Носов Н.Н.',
            year: '2015',
            isRead: false,
            page: '',
            imgSrc: 'data:image/jpeg;base64,'
          }
        ]
      }
    },
    methods: {
      sendForm() {
        const name = this.name;
        const author = this.author;
        const year = this.year;
        const imgSrc = this.imgSrc;
        const id = Math.floor(Math.random() * 100);

        this.books.push({
          id,
          name,
          author,
          year,
          imgSrc
        });
        this.name = '';
        this.author = '';
        this.year = '';
        this.imgSrc = '';
        this.dialog = false;
      },
      mounted() {
        console.log('App mounted!');
        const books = JSON.parse(localStorage.getItem('books'));
        if (books) {
          this.books = books;
        }
      },
      completeBook(id) {
        let book =  this.books.find( book => book.id === id)
        return book;
        console.log(book);
      }
    },
    watch: {
      books: {
        handler() {
          console.log('Books changed!');
          localStorage.setItem('books', JSON.stringify(this.books));
        },
        deep: true
      }
    }
  }
</script>

<style scoped>

</style>




